<template>
  <div class="row">
    <modal :show="modalStatus">
      <h6 class="modal-title" id="modal-title-default">Add Customer</h6>

      <p class="mt-4">Please Fill these data to add new <strong>Customer</strong></p>

      <form @submit.prevent="">
        <div class="d-flex flex-column">
          <input-component
            @input="name"
            type="text"
            placeholder="Customer Name"
            name="name"
            size="md"
            :value="form.name"
            :valid="formValidation.name.isValid"
            :startValidate="formValidation.name.startValidate"
            :errorMessage="formValidation.name.errorMessage"
          />
          <input-component
            @input="password"
            type="password"
            placeholder="Customer Password"
            name="password"
            size="md"
            :value="form.password"
            :valid="formValidation.password.isValid"
            :startValidate="formValidation.password.startValidate"
            :errorMessage="formValidation.password.errorMessage"
          />
          <input-component
            @input="email"
            type="email"
            placeholder="Customer Mail"
            name="email"
            size="md"
            :value="form.email"
            :valid="formValidation.email.isValid"
            :startValidate="formValidation.email.startValidate"
            :errorMessage="formValidation.email.errorMessage"
          />

          <input-component
            @input="phone"
            type="tel"
            placeholder="Customer Phone Number"
            name="Phone Number"
            size="md"
            :value="form.phone"
            :valid="formValidation.phone.isValid"
            :startValidate="formValidation.phone.startValidate"
            :errorMessage="formValidation.phone.errorMessage"
          />

          <div class="col-auto mb-3">
            <select v-model="form.governorate" class="form-select" id="autoSizingSelect">
              <option value="0">Choose Governorate ...</option>
              <option v-for="governorate in governorates" :key="governorate.id" :value="governorate">
                {{ governorate }}
              </option>
            </select>
          </div>

          <input-component
            @input="area"
            type="text"
            placeholder="Customer area"
            name="area"
            size="md"
            :value="form.area"
            :valid="formValidation.area.isValid"
            :startValidate="formValidation.area.startValidate"
            :errorMessage="formValidation.area.errorMessage"
          />

          <div class="mb-3">
            <label v-if="!fileUploadInProgress && !fileUploadInFailed" for="formFile" class="form-label">
              Customer Image
            </label>
            <label v-if="fileUploadInProgress" for="formFile" class="form-label">Uploading In Progress</label>
            <label v-if="fileUploadInFailed" for="formFile" class="form-label text-danger">Failed to Uplaod file</label>
            <input @change="fileSelect" class="form-control" type="file" id="formFile" />
          </div>
        </div>

        <div class="d-flex justify-content-between gap-1">
          <button-component
            class="mt-4"
            variant="gradient"
            color="success"
            size="sm"
            :isLoading="actionInProgress.loading"
            :disabled="actionInProgress.loading || fileUploadInProgress"
            @click="addCustomer()"
          >
            Submit
          </button-component>
          <button-component class="mt-4" variant="gradient" color="danger" size="sm" @click="toggleModal()">
            Close
          </button-component>
        </div>
      </form>
    </modal>
  </div>
</template>

<script>
  import Modal from '@/components/Modal-dialog';
  import ButtonComponent from '@/components/Button.vue';
  import InputComponent from '@/components/Input.vue';
  import axiosInstance from '../../../axios-instance';
  export default {
    name: 'add-customer',
    components: { Modal, ButtonComponent, InputComponent },
    data() {
      return {
        form: {
          name: '',
          phone: '',
          email: '',
          password: '',
          imageUrl: '/uploads/310e8766-e238-4e20-a83e-2b1c098242e0.png',
          governorate: '',
          area: '',
        },
        formValidation: {
          name: { isValid: false, startValidate: false, errorMessage: '' },
          phone: { isValid: false, startValidate: false, errorMessage: '' },
          email: { isValid: false, startValidate: false, errorMessage: '' },
          password: { isValid: false, startValidate: false, errorMessage: '' },
          governorate: { isValid: false, startValidate: false, errorMessage: '' },
          area: { isValid: false, startValidate: false, errorMessage: '' },
        },
        governorates: [
          'بغداد',
          'نينوى',
          'البصرة',
          'صلاح الدين',
          'دهوك',
          'أربيل',
          'السليمانية',
          'ديالى',
          'واسط',
          'ميسان',
          'ذي قار',
          'المثنى',
          'بابل',
          'كربلاء',
          'النجف',
          'الانبار',
          'الديوانية',
          'كركوك',
          'حلبجة',
        ],
        fileUploadInProgress: false,
        fileUploadInFailed: false,
        uploadedFileDetails: {},
      };
    },
    computed: {
      modalStatus() {
        return this.$store.state.CustomersStore.modals.addCustomer;
      },
      actionInProgress() {
        return this.$store.state.CustomersStore.progress.addCustomers;
      },
      formStatus() {
        return this.formValidation.name.isValid;
      },
    },

    methods: {
      name(event) {
        this.form.name = event.target.value;
      },
      phone(event) {
        this.form.phone = event.target.value;
      },
      email(event) {
        this.form.email = event.target.value;
      },
      password(event) {
        this.form.password = event.target.value;
      },
      area(event) {
        this.form.area = event.target.value;
      },
      fileSelect(event) {
        if (!event.target.files.length) return;
        this.fileUploadInProgress = true;
        this.fileUploadInFailed = false;
        const formData = new FormData();
        formData.append('file', event.target.files[0]);
        axiosInstance
          .post('files/upload', formData)
          .then((tResp) => {
            this.fileUploadInProgress = false;
            this.uploadedFileDetails = tResp.data;
          })
          .catch(() => {
            this.fileUploadInFailed = true;
            this.fileUploadInProgress = false;
          });
      },
      toggleModal() {
        this.$store.commit('CustomersStore/TOGGLE_ADD_CUSTOMER_MODAL');
      },
      startValidation() {
        this.formValidation.name.startValidate = true;
        this.formValidation.email.startValidate = true;
        this.formValidation.phone.startValidate = true;
        this.formValidation.password.startValidate = true;
      },
      checkForm() {
        this.formValidation.name.startValidate = true;
        if (!this.form.name.length) {
          this.formValidation.name.isValid = false;
          this.formValidation.name.errorMessage = 'Name Field is required';
        } else {
          this.formValidation.name.isValid = true;
        }
      },
      addCustomer() {
        this.checkForm();
        if (this.formStatus) {
          this.$store.dispatch('CustomersStore/addCustomer', this.form);
        }
      },
    },
  };
</script>
